<template>
  <div>
    <b-card
        bg-variant="light"
        title="자동 거래"
    >
      <b-form-group
          id="volumn-order-1"
          label="1회 최대 코인 개수"
          label-for="volumn-order-input-1"
          label-cols-sm="4"
          description="최대 코인 개수를 입력해주세요."
      >
        <b-form-input
            id="volumn-order-input-1"
            v-model="volumn_order.max_amount"
            type="text"
            placeholder="Enter Maximum amount"
            size="lg"
        ></b-form-input>
      </b-form-group>
      <b-form-group
          id="volumn-order-2"
          label="1회 최소 코인 개수"
          label-for="volumn-order-input-2"
          label-cols-sm="4"
          description="최소 코인 개수는 자동으로 입력 됩니다."
      >
        <b-form-input
            id="volumn-order-input-2"
            v-model="volumn_order.min_amount"
            type="text"
            placeholder="Enter Minimum amount"
            size="lg"
        ></b-form-input>
      </b-form-group>
      <b-button-group size="lg">
        <b-button variant="primary" @click="onStartVolume">시작</b-button>
        <b-button variant="danger" @click="onStopVolume">종료</b-button>
        <!--<b-button variant="danger" @click="getOrderBook">getOrderBook</b-button>-->
      </b-button-group>
    </b-card>
    >
  </div>
</template>

<script>
import axios from "axios";
import _ from "lodash";

export default {
  name: "VolumeOrder",
  props: {
    token: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      volumn_order: {
        max_amount: 0,
        min_amount: 0
      },
      info_data: {},
      sell_order: {
        market_id: 'PB-KRW',
        type: 'limit',
        side: 'sell',
        time_in_force: 'gtc',
        limit_price: '',
        cost: '',
        quantity: '',
        client_order_id: ''
      },
      buy_order: {
        market_id: 'PB-KRW',
        type: 'limit',
        side: 'buy',
        time_in_force: 'gtc',
        limit_price: '',
        cost: '',
        quantity: '',
        client_order_id: ''
      },
      polling: null,
      polling_token: null
    }
  },
  methods: {
    onStartVolume() {
      const self = this;
      this.polling = setInterval(() => {
        self.getOrderBook()
      }, 3000)
      this.polling_token = setInterval( () => {
        self.$emit('getToken')
      }, 60000)
    },
    onStopVolume() {
      clearInterval(this.polling)
      clearInterval(this.polling_token)
    },
    setOrder() {
      const self = this;
      axios.post(
          '/v1/new_order',
          _.omitBy(self.sell_order, _.isEmpty),
          {
            headers: {
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + self.token
            }
          })
          .then(function (resp) {
            self.$notify({
              duration: 3000,
              group: 'foo',
              title: 'success sell! - ' + self.getNow(),
              text: 'StatusText : ' + resp.statusText + '\n' +
                  'Status : ' + resp.status
            })
          })
          .catch(function (error) {
            if (error.response) {
              self.$notify({
                duration: 3000,
                group: 'foo',
                title: 'sell order fail! - ' + self.getNow(),
                text: 'ERROR : ' + error.response.data.errorCode + '\n' +
                    'Status : ' + error.response.status
              })
            }
          });
      axios.post(
          '/v1/new_order',
          _.omitBy(self.buy_order, _.isEmpty),
          {
            headers: {
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + self.token
            }
          })
          .then(function (resp) {
            self.$notify({
              duration: 3000,
              group: 'foo',
              title: 'success buy! - ' + self.getNow(),
              text: 'StatusText : ' + resp.statusText + '\n' +
                  'Status : ' + resp.status
            })
          })
          .catch(function (error) {
            if (error.response) {
              self.$notify({
                duration: 3000,
                group: 'foo',
                title: 'buy order fail! - ' + self.getNow(),
                text: 'ERROR : ' + error.response.data.errorCode + '\n' +
                    'Status : ' + error.response.status
              })
            }
          });
    },
    getOrderBook() {
      const self = this;
      axios.get('/v1/order_book', {
        params: {market_id: 'PB-KRW'}
      }).then(function (resp) {
        if (resp.status === 200) {
          let data = resp.data
          let result = _.map(_.groupBy(data.data, 'side'), function (array) {
            return _.orderBy(array, function (o) {
              return parseFloat(o.price);
            })
          })
          self.getBeetweenData(result)
          self.setOrder()
        }
      })
    },
    getBeetweenData(data) {
      let buyData = _.last(_.head(data))
      let sellData = _.head(_.last(data))
      let meanData = _.random(_.toNumber(sellData.price), _.toNumber(buyData.price)).toFixed(2)
      // let meanData = _.mean([_.toNumber(sellData.price), _.toNumber(buyData.price)]).toFixed(2)
      let divideData = _.toString(_.ceil(_.divide(1000, meanData)))
      let quantity = _.toNumber(this.volumn_order.max_amount) > 0 ? this.volumn_order.max_amount : divideData
      if (_.toNumber(this.volumn_order.max_amount) > 0 && _.toNumber(this.volumn_order.min_amount) > 0) {
        quantity = _.toString(_.random(_.toNumber(this.volumn_order.min_amount), _.toNumber(this.volumn_order.max_amount)))
      }
      this.sell_order.limit_price = meanData
      this.buy_order.limit_price = meanData
      this.sell_order.quantity = quantity
      this.buy_order.quantity = quantity
      this.volumn_order.min_amount = divideData
    },
    getNow() {
      const today = new Date();
      const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
      const time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + ' ' + time;
      return dateTime;
    }
  }
}
</script>

