<template>
  <b-form class="ml-5 mt-5 mr-5 mb-5" v-if="!hasToken">
    <b-form-group
        id="input-group-1"
        label="클라이언트 ID"
        label-for="input-1"
    >
      <b-form-input
          id="input-1"
          v-model="form.client_id"
          type="text"
          placeholder="Enter Client Id"
      ></b-form-input>
    </b-form-group>

    <b-form-group
        id="input-group-2"
        label="클라이언트 key"
        label-for="input-2"
    >
      <b-form-input
          id="input-2"
          v-model="form.client_key"
          type="password"
          placeholder="Enter Client Key"
      ></b-form-input>
    </b-form-group>

    <b-button @click="getToken">
      getToken
    </b-button>
  </b-form>
</template>

<script>

export default {
  name: "Token",
  data() {
    return {
      form: {
        client_id: '',
        client_key: '',
      },
    }
  },
  props: {
    hasToken : {
      type: Boolean, default: false
    }
  },
  methods: {
    getToken() {
      this.$emit("setToken", this.form.client_id, this.form.client_key)
      this.$emit('getToken')
    },
  }
}
</script>

