`<template>
  <div class="w-50 ml-5">
    <TokenComponent
        :has-token="token.hasToken"
        @setToken="setToken"
        @getToken="getToken"
    ></TokenComponent>
    <!-- 볼륨 주문 시작 -->
    <VolumeOrder
        v-if="token.hasToken"
        :token="token.token"
        @getToken="getToken"
    ></VolumeOrder>

<!--    &lt;!&ndash; 주문 시작 &ndash;&gt;-->
<!--    <OrderComponent-->
<!--        v-if="token.hasToken"-->
<!--        :token="token.token"-->
<!--        @onClickRequestToken="onClickRequestToken"-->
<!--    ></OrderComponent>-->
    <notifications
        group="foo"
    ></notifications>
  </div>
</template>

<script>
import TokenComponent from '@/components/Token'
import VolumeOrder from "@/components/VolumeOrder";
import axios from 'axios'

export default {
  components: {
    TokenComponent,
    VolumeOrder
  },
  created() {
  },
  data() {
    return {
      token: {
        hasToken: false,
        token: ''
      },
      info: {
        id: '',
        key: ''
      }
    }
  },
  mounted() {

  },
  methods: {
    onReset(evt) {
      evt.preventDefault()
      // Reset our form values
      this.form.client_id = ''
      this.form.client_key = ''
      // Trick to reset/clear native browser form validation state
    },
    getToken() {
      const self = this
      const authHeader = 'Basic ' + btoa(`${this.info.id}:${this.info.key}`);
      let data = JSON.stringify({
        grant_type: 'client_credentials'
      });
      axios.post('/token', data, {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': authHeader
        }
      }).then(function (resp) {
        if (resp.status === 200) {
          self.$notify({
            duration: 3000,
            group: 'foo',
            title: 'get token success',
            text: 'statusText : ' + resp.statusText +'\n' + 'status : ' + resp.status
          });
          self.token.hasToken = true
          self.token.token = resp.data.access_token
          console.log("getToken: ", self.token)
        }
      })
    },
    onClickRequestToken() {
      this.token.hasToken = !this.token.hasToken
    },

    setToken(id, key) {
      this.info.id = id;
      this.info.key = key;
    }
  }
}
</script>`